/* General Styling */
body {
    font-family: 'Arial', sans-serif;
    background-color: #f9f9f9;
    margin: 0;
    padding: 0;
  }
  
  .contact-container {
    padding: 30px;
    max-width: 800px;
    margin: 50px auto;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }
  
  .branch-fieldset {
    border: 2px solid #2c3e50;
    padding: 20px;
    border-radius: 8px;
  }
  
  .branch {
    font-size: 2em;
    color: #2c3e50;
    text-align: center;
  }
  
  .branch-section {
    margin-bottom: 20px;
  }
  
  .branch-section h2 {
    color: #34495e;
    font-size: 1.5em;
    margin-bottom: 10px;
  }
  
  .branch-section p {
    font-size: 1.1em;
    line-height: 1.6;
    color: #7f8c8d;
  }
  
  .branch-section hr {
    border: 0;
    border-top: 1px solid #ddd;
    margin: 20px 0;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .contact-container {
      padding: 20px;
      margin: 20px auto;
    }
  
    .branch {
      font-size: 1.8em;
    }
  
    .branch-section h2 {
      font-size: 1.3em;
    }
  
    .branch-section p {
      font-size: 1em;
    }
  }
  
  @media (max-width: 480px) {
    .contact-container {
      padding: 15px;
      margin: 15px auto;
    }
  
    .branch {
      font-size: 1.5em;
    }
  
    .branch-section h2 {
      font-size: 1.2em;
    }
  
    .branch-section p {
      font-size: 0.95em;
    }
  }
  