/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Sticky Element */
* {
    position: sticky;
}

/* Home Image Section */
.homeimage {
    text-align: center;
    align-items: center;
    display: flex;
    gap: 300px;
    flex-direction: row;
    width: 100%;
    padding: 1%;
    background-color: #394f97;
    background-image: url("https://wallpaperboat.com/wp-content/uploads/2020/12/02/62556/light-color-12.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: center;
    background-attachment: fixed;
}

.stop {
    clip-path: circle();
    float: right;
    width: 50%;
}

/* Word Section */
.word .first {
    margin-left: 19%;
    padding: 3%;
}

.word .second,
.word .third {
    margin-left: 19%;
    padding-left: 19%;
    padding: 3%;
}

/* Div Styling */
.div {
    background-color: blue;
}

/* Headings */
h3,
.max,
.max1,
.max2,
.max3,
.max4,
.iid,
.iiid,
.char {
    text-align: center;
    padding-bottom: 2%;
}

/* Click Button */
.click {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%; /* Full width */
  flex-direction: row;
  font-size: 30px;
  text-align: center;





}






button {
    padding: 18px;
    width: 100%; /* Full width */
    font-size: 16px;
}


.next {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 100%;
    padding-left: 4%;
    padding-right: 4%;
    overflow: hidden;
}

.next img {
    width: auto;
    max-height: 80px;
    object-fit: contain;
}

/* Marquee */
marquee {
    background-color: blue;
    padding: 4px;
}

/* Scrollable Container */
.scroll-container {
    display: flex;
    overflow-x: auto;
    white-space: nowrap;
    width: 100%;
    padding: 10px 0;
      
}

.subjects span {
    display: inline-block;
    margin-right: 15px;
}

.subjects img {
    display: flex;
    width: 350px;
    height: auto;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.scroll-container::-webkit-scrollbar {
    height: 8px;
}

.scroll-container::-webkit-scrollbar-thumb {
    background-color: #ccc;
    border-radius: 4px;
}

.scroll-container::-webkit-scrollbar-track {
    background-color: #f9f9f9;
}

/* Certificates Section */
.certificate {
    margin-top: 5%;
    text-align: center;
}

.certificate1,
.certificate2,
.certificate3,
.certificate4 {
    margin-top: 2%;
    text-align: center;
    justify-content: center;
    display: flex;
    gap: 6%;
}

/* Why Section */
.why {
    display: flex;
    position: relative;
}

.why > div {
    margin: 2px;
    padding: 25px;
    font-size: 15px;
}

.why > div:hover {
    background-color: #cec8c8da;
}

/* Footer */
footer {
    background-color: rgba(55, 51, 51, 0.806);
}

/* Responsive Design */
@media (max-width: 1200px) {
    .homeimage {
        flex-direction: column;
        gap: 1rem;
        padding: 10px;
    }

    .word .first,
    .word .second,
    .word .third {
        margin-left: 5%;
        padding: 3%;
    }

    .certificate1,
    .certificate2,
    .certificate3,
    .certificate4 {
        gap: 4%;
        flex-wrap: wrap;
    }

    .scroll-container img {
        width: 250px;
    }

    .next img {
        max-height: 60px;
    }
}

@media (max-width: 768px) {
    .homeimage {
        flex-direction: column;
        gap: 0.5rem;
        text-align: center;
        padding: 20px;
    }

    .word .first,
    .word .second,
    .word .third {
        text-align: center;
        margin: 0 auto;
    }

    .certificate1,
    .certificate2,
    .certificate3,
    .certificate4 {
        flex-wrap: wrap;
        gap: 20px;
        justify-content: center;
    }

    button {
        padding: 10px 20px;
        font-size: 14px;
    }
}

@media (max-width: 480px) {
    .homeimage {
        flex-direction: column;
        gap: 10px;
        padding: 10px;
        text-align: center;
    }

    .stop {
        clip-path: circle();
        width: 100px;
        margin: 0 auto;
    }

    button {
        padding: 8px 16px;
        font-size: 12px;
    }

    .word .first,
    .word .second,
    .word .third {
        text-align: center;
        margin: 0 auto;
        padding: 10px;
    }

    .scroll-container img {
        width: 150px;
    }

    .certificate1,
    .certificate2,
    .certificate3,
    .certificate4 {
        gap: 10px;
        flex-direction: column;
        align-items: center;
    }

    .why {
        flex-direction: column;
        align-items: center;
    }
}
