.display {
    display: grid;
}

#p {
    background-color: rgba(36, 35, 35, 0.908);
    color: white;
    text-decoration: none;
    width: 100%; /* Ensure the element takes full width */
    position: relative; /* Makes sure the background is stretched properly */
    left: 0; /* Align the element to the left edge */
    right: 0; /* Align the element to the right edge */
    padding: 20px;
    box-sizing: border-box; /* Padding should be inside the width */
}

/* Ensuring the background takes full width across screen for smaller devices */
@media (max-width: 768px) {
    #p {
        padding: 15px;
    }
}

@media (max-width: 480px) {
    #p {
        padding: 10px;
    }
}

div #p {
    display: flex;
    gap: 300px;
    flex-direction: row;
    width: 100%;
    padding: 4%;
    text-align: center;
    align-items: center;
    border: ridge black 5%;
}





.img {
    padding-right: 10%;
}

.main {
    padding-left: 1%;
    text-align: left;
    margin-left: 10%;
    border: 2px ridge rgb(48, 47, 47);
}

div .main h3 {
    text-align: left;
    padding-left: 2%;
}

.form {
    display: flex;
}

.enquiry .main {
    display: flex;
    flex-direction: column;
}

.enquiry {
    padding-top: 20px;
    border: 2px ridge rgb(49, 47, 47);
    width: 1200px;
    height: 500px;
    background-color: white;
    margin-right: 15%;
    margin-left: 1%;
    text-align: center;
}

input, select {
    height: 30px;
    width: 250px;
    padding: 1%;
    margin: 7%;
    margin-top: 1%;
}

