/* Responsive Design for Mobile Devices */
@media (max-width: 768px) {
    div #p {
        flex-direction: column;
        gap: 15px; /* Further reduce the gap */
    }

    .img {
        width: 100%; /* Full-width image for smaller screens */
        padding-right: 0;
    }

    .main {
        margin-left: 0%; /* Remove left margin for better centering */
        padding-left: 0%; /* Adjust padding */
    }

    .enquiry {
        width: 100%;
        margin-right: 0;
        margin-left: 0;
    }

    .form {
        flex-direction: column;
        align-items: center; /* Align form elements centrally */
    }

    input, select {
        width: 90%; /* Inputs take more space on mobile */
        margin: 10px; /* Adjust margin for mobile */
    }
}

/* Further Adjustments for Very Small Screens */
@media (max-width: 480px) {
    .enquiry {
        width: 100%;
        height: auto; /* Allow height to adjust to content */
        margin-right: 0;
        margin-left: 0;
    }

    input, select {
        width: 90%; /* Ensure inputs take full width */
        margin: 8px; 
    }
}
