/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, sans-serif;
  }
  
  /* Login Container */
  .login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f4f4f4; /* Light gray background */
    padding: 1rem;
  }
  
  /* Login Box */
  .login-box {
    background: #ffffff;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    width: 100%;
  }
  
  /* Title */
  .login-title {
    font-size: 1.8rem;
    color: #333333;
    text-align: center;
    margin-bottom: 1.5rem;
    font-weight: bold;
  }
  
  /* Form Group */
  .form-group {
    margin-bottom: 1.5rem;
  }
  
  .form-group label {
    display: block;
    font-size: 1rem;
    color: #666666;
    margin-bottom: 0.5rem;
  }
  
  .input-field {
    width: 100%;
    padding: 0.8rem;
    font-size: 1rem;
    border: 1px solid #dddddd;
    border-radius: 5px;
    transition: border-color 0.3s;
  }
  
  .input-field:focus {
    border-color: #007bff; /* Blue focus */
    outline: none;
  }
  
  /* Buttons */
  .btn {
    display: inline-block;
    width: 100%;
    padding: 0.8rem;
    font-size: 1rem;
    font-weight: bold;
    text-align: center;
    color: #ffffff;
    background-color: #007bff; /* Primary blue */
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
    text-decoration: none; /* Remove underline for links styled as buttons */
  }
  
  .btn:hover {
    background-color: #0056b3; /* Darker blue on hover */
  }
  
  /* Register Button */
  .register-button {
    margin-top: 1rem;
    background-color: #28a745; /* Green button for register */
  }
  
  .register-button:hover {
    background-color: #218838; /* Darker green on hover */
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .login-box {
      padding: 1.5rem;
    }
  
    .login-title {
      font-size: 1.5rem;
    }
  
    .btn {
      font-size: 0.9rem;
    }
  }
  