/* General Styling */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f9f9f9;
  }
  
  .product {
    text-align: center;
    margin: 50px auto;
    padding: 20px;
    max-width: 500px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }
  
  .product p {
    font-size: 1.2em;
    color: #2c3e50;
    margin-bottom: 20px;
  }
  
  .certificate-input {
    padding: 10px;
    margin: 10px 10px 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1em;
    width: calc(100% - 160px); /* Dynamically resizes with the container */
    max-width: 300px; /* Limit width on larger screens */
  }
  
  .certificate-input:focus {
    border-color: #007BFF;
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  }
  
  .submit {
    padding: 10px 20px;
    background-color: #007BFF;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    font-size: 1em;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .submit:hover {
    background-color: #0056b3;
  }
  
  /* Responsive Design */
  @media (max-width: 480px) {
    .certificate-input {
      width: 100%; /* Full width for small screens */
      margin-bottom: 10px;
    }
  
    .submit {
      width: 100%;
    }
  }
  