/* General Styles */
body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
  }
  
  /* Container */
  .student-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
  
  /* Login Box */
  .login-box {
    background: #ffffff;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
  }
  
  /* Title */
  .login-box h2 {
    text-align: center;
    color: #333;
    margin-bottom: 1.5rem;
  }
  
  /* Input Group */
  .input-group {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 0.5rem;
    background: #f9f9f9;
    transition: border-color 0.3s ease;
  }
  
  .input-group:hover {
    border-color: #007bff;
  }
  
  .input-group i {
    font-size: 1rem;
    color: #888;
    margin-right: 0.5rem;
  }
  
  .input-field {
    border: none;
    outline: none;
    background: transparent;
    font-size: 1rem;
    width: 100%;
    padding: 0.5rem 0;
  }
  
  /* Input Hint */
  .input-hint {
    display: block;
    font-size: 0.8rem;
    color: #666;
    margin-bottom: 1rem;
  }
  
  /* Button */
  .submit-button {
    width: 100%;
    background: #007bff;
    color: white;
    padding: 0.8rem;
    font-size: 1rem;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .submit-button:hover {
    background: #0056b3;
  }
  
  /* Responsive */
  @media (max-width: 768px) {
    .login-box {
      padding: 1.5rem;
    }
  }
  