/* General Styling */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f0f4f8;
  margin: 0;
  padding: 0;
}

/* Container Styling */
.container {
  padding: 30px;
  max-width: 1200px;
  margin: 50px auto;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

/* Banner Image */
.banner-image {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
}

/* Titles */
.title {
  color: #2c3e50;
  font-size: 2.5em;
  margin-bottom: 15px;
  text-align: center;
}

.subtitle {
  color: #34495e;
  font-size: 1.5em;
  margin-bottom: 15px;
  text-align: center;
}

/* Description */
.description {
  font-size: 1.1em;
  margin-bottom: 25px;
  line-height: 1.6;
  color: #34495e;
  text-align: justify;
}

/* Footer Styling */
.footer {
  padding: 20px 0;
  text-align: center;
  background-color: #f9f9f9;
  border-top: 1px solid #ccc;
  color: #7f8c8d;
  font-size: 0.9em;
}

/* Responsive Design */
@media (max-width: 768px) {
  .title {
    font-size: 2em;
  }

  .subtitle {
    font-size: 1.3em;
  }

  .description {
    font-size: 1em;
  }

  .container {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .title {
    font-size: 1.8em;
  }

  .subtitle {
    font-size: 1.2em;
  }

  .description {
    font-size: 0.95em;
    line-height: 1.5;
  }

  .container {
    padding: 15px;
  }
}
