* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/* Navbar */
.nav img {
    float: left;
    margin-left: 3%;
    max-width: 100px; /* Ensure logo resizes dynamically */
    height: auto;
}

.nav {
    margin-right: 12%;
    text-align: right;
}

.nav ul {
    list-style: none;
}

.nav ul li {
    display: inline-block;
    margin: 0 5px;
}

.nav ul li button {
    padding: 10px 20px;
    background-color: white;
    border: none;
    border-radius: 15px;
    font-size: 20px;
    transition: background-color 0.3s;
}

.nav ul li button:hover {
    background-color: #354ea0;
    color: white;
}

.nav ul li button:active {
    background-color: blue;
}

/* Dropdown */
.drop {
    position: relative;
    display: inline-block;
}

.drop span {
    float: left;
    text-align: end;
    justify-content: end;
    margin-left: 5%;
    padding-top: 4%;
}

.down {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
    padding: 12px 16px;
    z-index: 1;
}

.drop:hover .down {
    display: block;
}

/* About Section */
.about {
    padding-top: 3%;
    padding-left: 2%;
}

/* User Section */
.user {
    background-color: rgba(170, 206, 215, 0.717);
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 10% auto;
    width: 80%;
    max-width: 400px;
    border: solid rgba(75, 73, 73, 0.532) 2px;
    border-radius: 10px;
    padding: 20px;
}

.user button {
    margin: 1%;
    margin-left: auto;
    margin-right: auto;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #354ea0;
    color: white;
    font-size: 16px;
    cursor: pointer;
}

.user button:hover {
    background-color: blue;
}

/* Responsive Design */
@media (max-width: 1200px) {
    .nav img {
        max-width: 80px;
    }

    .nav ul li button {
        font-size: 18px;
        padding: 8px 16px;
    }

    .drop span {
        margin-left: 3%;
    }

    .user {
        margin-top: 5%;
        width: 90%;
    }
}

@media (max-width: 768px) {
    .nav {
        margin-right: 5%;
        text-align: center;
    }

    .nav ul {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding: 10px 0;
    }

    .nav ul li {
        margin: 5px;
    }

    .drop span {
        text-align: center;
        margin-left: 0;
    }

    .user {
        margin-top: 3%;
    }
}

@media (max-width: 480px) {
    .nav img {
        max-width: 60px;
    }

    .nav ul li button {
        font-size: 14px;
        padding: 6px 12px;
    }

    .user button {
        font-size: 14px;
        padding: 8px 16px;
    }

    .drop span {
        font-size: 14px;
        margin-left: 2%;
    }
}
